const directionsButtons = document.querySelectorAll('.directions__item-button');
const sliderCloseBtn = document.querySelector('.projects-slider__close');
const projectsCardsLinks = document.querySelectorAll('.projects__item-link');
const projectsSlider = document.querySelector('.projects-slider');

directionsButtons.forEach(button => {
	button.addEventListener('click', (event) => {
		event.target.closest('.directions__item').classList.toggle('active');
	})
})

sliderCloseBtn.addEventListener('click', (event) => {
	projectsSlider.classList.remove('active');
})

projectsCardsLinks.forEach(link => {
	link.addEventListener('click', (event) => {
		event.preventDefault();
		projectsSlider.classList.add('active');
	})
})
